export default "@import '../../themes/src/default.css';\n\n:root {\n    --notification-desktop-content-width: 278px;\n}\n\n.notificationComponent {\n    visibility: hidden;\n    position: fixed;\n    right: var(--gap-s);\n    transform: translate(0, -500px);\n    display: inline-flex;\n    width: calc(100% - var(--gap-xl));\n    max-width: calc(100vw - var(--gap-xl));\n    user-select: none;\n    transition: transform 0.4s ease-out;\n\n    @media screen and (min-width: 600px) {\n        right: var(--gap-4xl);\n        width: auto;\n        transform: translate(calc(100% + var(--gap-4xl)), 0);\n    }\n\n    &.isVisible {\n        visibility: visible;\n        transform: translate(0, 0);\n    }\n\n    &.isClosing {\n        transition: transform 0.1s ease-out;\n        transform: translate(100vw, 0);\n\n        @media screen and (min-width: 600px) {\n            transform: translate(calc(100% + var(--gap-4xl)), 0);\n        }\n    }\n}\n\n.toastContent {\n    @media screen and (min-width: 600px) {\n        width: var(--notification-desktop-content-width);\n    }\n}\n\n.actionSection {\n    min-width: 104px;\n    min-height: 48px;\n    padding: 0 var(--gap-xs);\n    margin: var(--gap-s-neg) 0;\n}\n";